import { useState, useEffect } from "react";
import "./index.css";
import {
  Sidebar,
  SidebarProvider,
  SidebarTrigger,
} from "./components/ui/sidebar";
import { AppSidebar } from "./components/Appsidebar";
import projectService from "./services/project";
import Projectdialog from "./components/ProjectDialog";
import Maincomponent from "./components/Maincomponent";
import Project from "./components/Project";

const App = () => {
  const [projects, setProjects] = useState([]);
  const [errorMessage, setErrorMessage] = useState("");
  const [infoMessage, setInfoMessage] = useState("");
  const [user, setUser] = useState(null);
  const [showLogin, setShowLogin] = useState(false)
  const [showSignup, setShowSignup] = useState(false)
  

  useEffect(() => {
    if (user) {
      projectService.getAll().then((projectsList) => {
        setProjects(projectsList);
        console.log(projectsList); // Log the fetched projects instead of the state
      }).catch((error) => {
        console.error("Failed to fetch projects:", error);
        setErrorMessage("Failed to fetch projects");
      });
    }
  }, [user]); // Add user to the dependency array

  useEffect(() => {
    const loggedInUserJson = window.localStorage.getItem('loggedInUser')
    if (loggedInUserJson) {
      const loggedInUser = JSON.parse(loggedInUserJson)
      setUser(loggedInUser)
      projectService.setToken(loggedInUser.token)
    }
  },[])

  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const handleDialogToggle = () => {
    setIsDialogOpen(!isDialogOpen);
  };

  const addProject = (projectName) => {
    const projectData = {
      name: projectName,
    };
    projectService
      .create(projectData)
      .then((response) => {
        setProjects(projects.concat(response));
      })
      .catch((error) => console.log(error));
    setIsDialogOpen(false);
  };

  const [isHomePageOpen, setIsHomePageOpen] = useState(true);
  const [selectedProject, setSelectedProject] = useState(projects[0]);

  return (
    <div className="flex h-screen">
      <SidebarProvider>
        <AppSidebar
          projects={projects}
          setProjects={setProjects}
          handleDialogToggle={handleDialogToggle}
          setIsHomePageOpen={setIsHomePageOpen}
          selectedProject={selectedProject}
          setSelectedProject={setSelectedProject}
          user={user}
          setUser={setUser}
          showLogin={showLogin}
          setShowLogin={setShowLogin}
          setShowSignup={setShowSignup}
          showSignup={showSignup}
        />
        <main className="flex-1 p-4">
          <SidebarTrigger />
          {isHomePageOpen && (
            <Maincomponent
              isHomePageOpen={isHomePageOpen}
              setIsHomePageOpen={setIsHomePageOpen}
              setSelectedProject={setSelectedProject}
              projects={projects}
              errorMessage={errorMessage}
              setErrorMessage={setErrorMessage}
              user={user}
              setUser={setUser}
              infoMessage={infoMessage}
              setInfoMessage={setInfoMessage}
              showLogin={showLogin}
              setShowLogin={setShowLogin}
              showSignup={showSignup}
              setShowSignup={setShowSignup}
            />
          )}
          {!isHomePageOpen && <Project project={selectedProject} />}
        </main>
      </SidebarProvider>
      {isDialogOpen && (
        <Projectdialog
          isDialogOpen={isDialogOpen}
          setIsDialogOpen={setIsDialogOpen}
          addProject={addProject}
        />
      )}
    </div>
  );
};

export default App;
