import * as React from "react";
import { useState } from "react";

import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuLabel,
  DropdownMenuRadioGroup,
  DropdownMenuRadioItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";

const PriorityDropdown = () => {
  const priorities = [
    { name: "Priority1", color: "red" },
    { name: "Priority2", color: "orange" },
    { name: "Priority3", color: "yellow" },
    { name: "Priority4", color: "green" },
  ];
  const [priority, setPriority] = useState("Priority")
  const handlePriorityChange = (event) => {
    setPriority(event.target.value)
  }
  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button variant="outline">{priority}</Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent>
        <DropdownMenuLabel>Priority</DropdownMenuLabel>
        <DropdownMenuSeparator />
        <DropdownMenuRadioGroup value={priority} onChange={handlePriorityChange}>
          {priorities.map((priority) => (
            <DropdownMenuRadioItem key={priority.name} className = {`bg-${priority.color}-400`}>
              {priority.name}
            </DropdownMenuRadioItem>
          ))}
        </DropdownMenuRadioGroup>
      </DropdownMenuContent>
    </DropdownMenu>
  );
};

export default PriorityDropdown;
