const baseURL = `/todos`;
import axios from "axios";

const getAllTodos = (projectId) => {
    const request = axios.get(`${baseURL}/${projectId}`)
    return request.then(response => response.data)
}

const saveTodo = (projectId, data) => {
    const request = axios.post(`${baseURL}/${projectId}`, data)
    return request.then(response => response.data)
}

const deleteTodo = (todoId) => {
    const request = axios.delete(`${baseURL}/${todoId}`)
    return request.then(response => response.data)
}

export {getAllTodos, saveTodo, deleteTodo}