import { Checkbox } from "./ui/checkbox";

const Todo = ({ todo, deleteTask }) => {
  return (
    <div className="flex items-center justify-between p-4 mb-2 bg-white rounded-lg shadow hover:shadow-md transition-shadow duration-200">
      <div className="flex items-center">
        <Checkbox className="mr-3" onClick={() => deleteTask(todo.id)} />
        <span className="text-gray-800 font-medium">{todo.name}</span>
      </div>

      <button className="text-green-300 bg-slate-200">
        Show details
      </button>

      <button
        onClick={() => deleteTask(todo.id)}
        className="text-sm text-red-500 hover:text-red-600 transition-colors duration-200"
      >
        Delete
      </button>
    </div>
  );
};

export default Todo;
