import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import PriorityDropdown from "./PriorityDropdown";
import { DatePicker } from "./Datepicker";
import { add } from "date-fns";

const TodoDialog = ({isDialogOpen, setIsDialogOpen, addTask, project}) => {  
    const [name, setName] = useState("")
    const handleNameInputChange = (event) => {
        setName(event.target.value)
    }

    const [description, setDescription] = useState("")
    const handleDescriptionChange = (event) => {
        setDescription(event.target.value)
    }

    const [priority, setPriority] = useState("")
    const handlePriorityChange = (event) => {
        setPriority(event.target.value)
    }

    const [dueDate, setDueDate] = useState("")
    const handleDueDateChange = (event) => {
        setDueDate(event.target.value)
    }

    const handleSubmit = (event) => {
        event.preventDefault()
        addTask(name, description, priority, dueDate, project.id)
        setIsDialogOpen(false)
    }

    return (
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
            <DialogContent>
                <DialogHeader>
                    <DialogTitle>Add tasks</DialogTitle>
                    <DialogDescription>Enter details for your task</DialogDescription>
                </DialogHeader>
                <form onSubmit={handleSubmit}>
                    <div>
                        <Label>Task Name</Label>
                        <Input value={name} onChange={handleNameInputChange}></Input>
                    </div>
                    <div>
                        <Label>Task Description</Label>
                        <Input onChange={handleDescriptionChange} value={description}></Input>
                    </div>
                    <div>
                        <Label>Task Priority</Label>
                        {/* <Button className="bg-white text-black" >Priority</Button> */}
                        <PriorityDropdown value={priority} onChange={handlePriorityChange} />
                    </div>
                    <div>
                        <Label>Due Date</Label>
                        <DatePicker onChange={handleDueDateChange} value={dueDate}></DatePicker>
                    </div>
                    <DialogFooter>
                        <Button type="submit">Save</Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    )
}

export default TodoDialog