import { Calendar, Home, Inbox, Search, Settings } from "lucide-react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
import {
  Sidebar,
  SidebarContent,
  SidebarGroup,
  SidebarGroupContent,
  SidebarGroupLabel,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
  SidebarGroupAction,
} from "@/components/ui/sidebar";
import { Button } from "./ui/button";
import Projectdialog from "./ProjectDialog";
import { useEffect, useState } from "react";
import { getAllTodos } from "@/services/todo";
import projectService from "@/services/project";

export function AppSidebar({
  projects,
  setProjects,
  handleDialogToggle,
  setIsHomePageOpen,
  selectedProject,
  setSelectedProject,
  user,
  setUser,
  showLogin,
  setShowLogin,
  showSignup, 
  setShowSignup
}) {
  const handleHomePageOpen = () => {
    setIsHomePageOpen(true);
  };

  useEffect(() => {
    if (selectedProject) {
      getAllTodos(selectedProject.id).then((todos) => {
        console.log(todos);
      });
    }
  }, [selectedProject]);

  const handleMenuButtonClick = (project) => {
    setSelectedProject(project);
    setIsHomePageOpen(false);
  };

  const menuItems = [
    {
      name: "Home",
      icon: Home,
      onClick: handleHomePageOpen,
    }
  ];

  const handleProjectDeletion = async (project) => {
    await setSelectedProject(project)
    console.log(selectedProject);
    await projectService.deleteProject(project.id)
    const newProjects = projects.filter(p => p.id !== project.id)
    setProjects(newProjects)
  }

  const handleLogOut = () => {
    window.localStorage.removeItem('loggedInUser')
    setUser(null)
    setIsHomePageOpen(true)
  }

  return (
    <Sidebar variant="floating">
      <SidebarContent>
        <SidebarGroup>
          <SidebarGroupContent>
            <SidebarMenu>
              {menuItems.map((menuItem) => (
                <SidebarMenuItem key={menuItem.name}>
                  <SidebarMenuButton onClick={menuItem.onClick}>
                    <menuItem.icon />
                    <span>{menuItem.name}</span>
                  </SidebarMenuButton>
                </SidebarMenuItem>
              ))}
            </SidebarMenu>
          </SidebarGroupContent>
        </SidebarGroup>
        {user && (
          <SidebarGroup>
            <SidebarGroupLabel>Projects</SidebarGroupLabel>
            <SidebarGroupAction
              title="Add Project"
              className="flex justify-center"
              onClick={handleDialogToggle}
            >
              <FontAwesomeIcon icon={faPlus} />
            </SidebarGroupAction>
            <SidebarGroupContent>
              <SidebarMenu>
                {projects.map((project) => (
                  <SidebarMenuItem
                    key={project.name}
                    className="flex cursor-pointer justify-center items-center"
                  >
                    <SidebarMenuButton
                      asChild
                      className="cursor-pointer"
                      onClick={() => handleMenuButtonClick(project)}
                    >
                      <span>{project.name}</span>
                    </SidebarMenuButton>
                    <SidebarMenuButton onClick={() => { handleProjectDeletion(project) }}>
                      <FontAwesomeIcon icon={faTrash} />
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                ))}
                <SidebarMenuButton onClick={handleLogOut}>Log out</SidebarMenuButton>
              </SidebarMenu>
            </SidebarGroupContent>
          </SidebarGroup>
        )}
        {!user && (
          <SidebarGroup>
            <SidebarMenuItem>
              <SidebarMenuButton onClick={() => { setShowLogin(true); setShowSignup(false)}}>Log In</SidebarMenuButton>
              <SidebarMenuButton onClick={() => { setShowSignup(true); setShowLogin(false)}}>Sign up</SidebarMenuButton>
            </SidebarMenuItem>
          </SidebarGroup>
        )}
      </SidebarContent>
    </Sidebar>
  );
}
