import { useEffect, useState } from "react";
import Todo from "./Todo";
import { deleteTodo, getAllTodos, saveTodo } from "@/services/todo";
import { Button } from "./ui/button";
import TodoDialog from "./TodoDialog";

const Project = ({ project }) => {
  const [todos, setTodos] = useState([]);
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  useEffect(() => {
    getAllTodos(project.id)
      .then((todoList) => setTodos(todoList))
      .catch((error) => console.log(error));
  }, [project.id]);

  const handleDialogToggle = () => {
    setIsDialogOpen(!isDialogOpen);
  };

  const addTask = (todoName, todoDescription, priority, dueDate) => {
    const newTodo = {
      name: todoName,
      description: todoDescription,
      priority: priority,
      dueDate: dueDate,
      projectId: project.id,
    };
    saveTodo(project.id, newTodo)
      .then((todo) => setTodos((prevTodos) => prevTodos.concat(todo)))
      .catch((error) => console.log(error));
  };

  const deleteTask = (todoId) => {
    deleteTodo(todoId)
      .then(() => {
        const newTodos = todos.filter((todo) => todo.id !== todoId);
        setTodos(newTodos);
      })
      .catch((error) => console.log(error));
  };

  return (
    <div className="max-w-2xl mx-auto p-6 bg-gray-50 rounded-lg shadow-lg">
      <h1 className="text-3xl font-bold text-gray-800 mb-6">{project.name}</h1>

      <Button
        onClick={handleDialogToggle}
        className="mb-6 bg-purple-600 text-white hover:bg-purple-700 transition duration-300"
      >
        Add Task
      </Button>

      <ul className="space-y-4">
        {todos.map((todo) => (
          <Todo key={todo.id} todo={todo} deleteTask={deleteTask} />
        ))}
      </ul>

      <TodoDialog
        isDialogOpen={isDialogOpen}
        setIsDialogOpen={setIsDialogOpen}
        addTask={addTask}
        project={project}
      />
    </div>
  );
};

export default Project;
